<template>
  <div>
    <v-app-bar
        app
        color="primary"
        dark
      >
      <div class="d-flex align-center">
        <v-btn outlined @click.stop="drawer = !drawer" small>E-Triage Dashboard</v-btn>
      </div>
      <v-spacer></v-spacer>
      <div class="d-flex align-center">
        <a href="#" @click="subscribe" v-if="this.isLoggedIn"><v-icon class="mx-2" >fas fa-bell</v-icon></a>
        <!-- <a class="pl-3"  href="#" @click="toInfirmary" v-if="this.isInfirmary && this.isLoggedIn"><v-icon class="mx-2" >fas fa-hospital-user</v-icon></a> -->
        <a href="#" @click="logout" v-if="this.isLoggedIn"><v-icon class="mx-2" >fas fa-sign-out-alt</v-icon></a>
      </div>
      
    </v-app-bar>
  
    <v-navigation-drawer
      v-model="drawer"
      fixed
      temporary
      v-if="isLoggedIn"
      color="primary"
      dark
    >
      <v-list dense>
        <v-list-item>
          <v-btn outlined @click.stop="drawer = !drawer" small>E-Triage Dashboard</v-btn>
        </v-list-item>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          link
          :to="item.route"
        >
          <v-list-item-icon style="width:24px">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      drawer: null,
      items: [
        { title: 'DASHBOARD', icon: 'fa fa-home', route: '/main' },
        { title: 'INFIRMARY', icon: 'fa fa-hospital-user', route: '/infirmary' },
        { title: 'ANALYTICS', icon: 'fa fa-chart-line', route: '/analytics' },
      ],
    }
  },
  computed: {
    ...mapGetters(["isLoggedIn","isInfirmary","userDetails"]),
  },
  methods: {
    async logout(){
      await this.$store.dispatch("logoutEmployee");
      this.$router.push("/");
    },
    async toInfirmary() {
      this.$router.push("/infirmary");
    },
    async subscribe() {
      await this.$store.dispatch("subscribeUser");
    }
  },
};
</script>